import './App.css';
import React, {useState, useRef} from 'react'
import NavBar from './component/NavBar';
import NewsCard from './component/NewsCard';
import { QueryClient, QueryClientProvider } from 'react-query'

const queryClient = new QueryClient()

function App() {

  const myRef = useRef()

  const [searchVal, setSearchVal] = useState('apple');

  


  return (
    <QueryClientProvider client={queryClient}>
      <NavBar searchVal={searchVal } seVal={setSearchVal} refetch= {() => myRef.current.refetch()}/>
      <NewsCard ref={myRef} searchVal={searchVal }  seVal={setSearchVal}/>
    </QueryClientProvider>
  );
}

export default App;
