import React from 'react'
import NavBar from '../component/NavBar'

const About = () => {
  return (
    <>
      <NavBar/>
      <div className='text-slate-300 flex justify-center'>
        <div className='block w-[90%] text-2xl rounded-lg mt-10 p-6 shadow-lg dark:bg-transparent text-justify border-b-2 border-slate-400'>
          Welcome to my news app! As a developer, I wanted to showcase my skills and expertise by building a project that reflects my passion. <br/><br/> This app serves as a personal project that I have created for my portfolio.

          The app uses the gNews API, which provides access to news articles from various sources. Although the free plan only allows for 100 requests per day, I believe this provides ample access to news articles for those who want to stay up-to-date on current events.
          <br/><br/>
          Please note that if you receive an "Error Occured" message, it is because the daily quota for API requests has been reached. The quota will reset at 00:00 UTC.
          <br/><br/>
          This news app is solely for showcase purposes and is not intended for public use. It serves as an example of my coding abilities and my dedication to creating high-quality projects.
          <br/><br/>
          Thank you for taking the time to explore my news app, and please feel free to contact me with any feedback or questions you may have. I hope this app serves as a testament to my skills and dedication to the world of software development.
        </div>
      </div>
    </>
    
  )
}

export default About
